@import url("https://fonts.googleapis.com/css2?family=Crimson+Text&family=Montserrat+Alternates:wght@300&family=Roboto:wght@300&display=swap");

.skillcontainer {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 100px 0;
}
.skillcontainer_text {
  color: white;
  flex: 0.6;
  text-align: left;
  max-width: 500px;
  margin-bottom: 20px;
}
.skillcontainer_text > h2 {
  font-size: 30px;
}

.skillcontainer_image {
  flex: 0.4;
  text-align: center;
  margin-right: 100px;
}

.skillcontainer_image > img {
  width: 600px;
  border-radius: 3px;
  object-fit: contain;
}
img {
  background-position: left;
  width: 375px;
  height: 300px;
  /* mix-blend-mode: multiply; */
}
.skillcontainer_skillsets > h5 {
  font-family: "Montserrat", sans-serif;
  /* font-weight: 400; */
  padding-top: 20px;
  padding-bottom: 5px;
}

